import React from 'react';
import PropTypes from 'prop-types';

class Checkbox extends React.Component {
  render() {
    return (
      <div className={`checkbox ${this.props.containerClass}`}>
        <label
          className={`custom-checkbox ${this.props.labelClass}`}
          htmlFor={this.props.id}
        >
          <input
            type="checkbox"
            className={`custom-control-input ${this.props.checkClass}`}
            id={this.props.id}
            name={this.props.id}
            onChange={(!this.props.disabled && this.props.onChange) ? this.props.onChange : () => {}}
            value={this.props.value}
            checked={this.props.checked}
            required={this.props.required}
            disabled={this.props.disabled}
          />
          <span className={`custom-control-indicator`} />
          <span className={`custom-control-description ${this.props.textClass}`}>{this.props.label}</span>
        </label>
      </div>
    );
  }
}

Checkbox.propTypes = {
  containerClass: PropTypes.string,
  checkClass: PropTypes.string,
  id: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool
  ]),
  checked: PropTypes.bool,
  onChange: PropTypes.func,
  required: PropTypes.bool,
  disabled: PropTypes.bool,
  label: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element
  ]),
  labelClass: PropTypes.string,
  textClass: PropTypes.string
};
Checkbox.defaultProps = {
  containerClass: '',
  checkClass: '',
  required: false,
  checked: false,
  label: '',
  labelClass: '',
  textClass: ''
};

export default Checkbox;
